  html {
    font-family: 'Lato', sans-serif;
    font-weight: 300;
  }

  a {
      text-decoration: none;
      color: inherit;
      margin: 0;
      padding: 0;
  }

  a:hover {
      color: inherit;
  }

  .title {
    font-family: 'Playfair Display', serif;
  }

  .boxy {
      box-sizing: border-box;
      position: relative;
      overflow: hidden;
  }

  .boxy a {
    text-decoration: none;
    font-size: 50px;
    line-height: 0;
    overflow: auto;
  }

  img.blurred {
    transition: transform .3s;
    transform: scale(1.03);
  }
  
  img.blurred:hover {
    transform: scale(1.16);
  }

  .slider {
    display: flex;
    overflow-x: auto;
    width: 300px;
    height: 300px;
    -webkit-overflow-scrolling: touch;
  }
  
  .slide {
    width: 300px;
    height: 100%;
  }

  img.grey {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%) brightness(150%);
  }

  .carousel-container {
    width: 100%;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    background-color: #333;
    overflow: hidden;
  }
  
  .current-image {
    width: auto;
    height: 100%;
  }
  
  .next-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100px;
    position: absolute;
    bottom: 10px;
    left: 0;
    display: flex;
    align-items: center;
    overflow: hidden;
    transition: all .5s;
  }
  
  .next-list li {
    display: inline-block;
    padding: 0;
    margin: 0;
    width: 100px;
    min-width: 100px;
    height: 51px;
    margin: 0 5px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: all .5s;
  }
  .next-list li:first-of-type {
    padding-left: 15px;
  }
  .next-list li:last-of-type {
    padding-right: 15px;
  }
  
  .image-of-list {
    width: 100%;
    height: 100%;
    opacity: 0.5;
    box-shadow: 0 0 20px #000;
    transition: all .5s;
  }
  
  .current-image-list {
    opacity: 1;
    transition: all .5s;
  }
  
  .arrow {
    display: flex;
    position: absolute;
    width: 50px;
    height: 100px;
    bottom: 10px;
    flex: 1;
    text-align: center;
    line-height: 50px;
    color: #FFF;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
  }
  
  
  .arrow-left {
    left: 10px;
  }
  .arrow-right {
    right: 10px;
  }
  
   /*
   * Animations
   */
  
   @keyframes fadeIn {
       0% {opacity: 0}
       100% {opacity: 1}
   }
  
   @keyframes slideInFromLeft {
       0% {
         opacity: 0;
         transform: translateX(-100%);
        }
       100% {
         opacity: 1;
         transform: translateX(0px);
       }
   }
  
   @keyframes slideInFromRight {
       0% {
         opacity: 0;
         transform: translateX(100%);
        }
       100% {
         opacity: 1;
         transform: translateX(0px);
       }
   }
  
   .slideInFromLeft {
     animation: slideInFromLeft .5s ease-in-out;
   }
  
   .slideInFromRight {
     animation: slideInFromRight .5s ease-in-out;
   }
  
   .fadeIn {
     animation: fadeIn .5s ease-in-out;
   }


